import { NETWORKS_INFORMATION } from './network.js';

export function formatExplorerLink(type, data) {
    switch (type) {
        case "Account": {
            const [chainId, address] = data;
            return `https://${NETWORKS_INFORMATION[chainId].explorer}/address/${address}`;
        }
        case "Transaction": {
            const [chainId, hash] = data;
            return `https://${NETWORKS_INFORMATION[chainId].explorer}/tx/${hash}`;
        }
        default: {

        }
    }
}

export function shortenHex(hex, front = 4, back = 5) {
    return `${hex.substring(0, front + 2)}â€¦${hex.substring(hex.length - back)}`;
}