import { useState } from "react";
import { Link } from "react-router-dom";

const Menu = ({ isConnected }) => {
    const [selectedButton, setSelectedButton] = useState(0); // 0 = Home, 1 = Ternader, 2 = TeamKill, 3 = ArbiTraitor

    const selectedButtonStyling = {
        backgroundColor: "#000000",
        color: "#FFFFFF"
    };

    if(isConnected) {
        return (
            <div className="menu-container">
                <ul>
                    <Link to="/" onClick={ () => setSelectedButton(0) }>
                        <li className="menu-item" style={selectedButton === 0 ? selectedButtonStyling : {}}>
                            Home
                        </li>
                    </Link>
                    <Link to="/" onClick={ () => setSelectedButton(0) }>
                        <li className="menu-item" style={selectedButton === 0 ? selectedButtonStyling : {}}>
                            Ternader
                        </li>
                    </Link>
                    <Link to="/" onClick={ () => setSelectedButton(0) }>
                        <li className="menu-item" style={selectedButton === 0 ? selectedButtonStyling : {}}>
                            TeamKill
                        </li>
                    </Link>
                    <Link to="/" onClick={ () => setSelectedButton(0) }>
                        <li className="menu-item" style={selectedButton === 0 ? selectedButtonStyling : {}}>
                            ArbiTraitor
                        </li>
                    </Link>
                </ul>
            </div>
        );
    } else {
        return (
            <div className="menu-container">
                <ul>
                    <li>fasdfs</li>
                </ul>
            </div>
        );
    }
}

export default Menu;