import MetaMaskOnboarding from "@metamask/onboarding";
import { useWeb3React } from "@web3-react/core";
import { UserRejectedRequestError } from "@web3-react/injected-connector";
import { useEffect, useLayoutEffect, useRef } from "react";
import useENSName from "../hooks/network/useENSName";
import { formatExplorerLink, shortenHex } from "../helpers/utils";
import { injected } from "../hooks/network/useInjected";

const Account = ({ triedToEagerConnect }) => {
  const {
    active,
    error,
    activate,
    chainId,
    account,
    setError,
  } = useWeb3React();

  // initialize metamask onboarding
  const onboarding = useRef();

  useLayoutEffect(() => {
    onboarding.current = new MetaMaskOnboarding();
  }, []);

  // manage connecting state for injected connector
  useEffect(() => {
    if (active || error) {
      onboarding.current?.stopOnboarding();
    }
  }, [active, error]);

  const ENSName = useENSName(account);

  if (error) {
    console.log(error);
  }

  if (typeof account !== "string") {
    const hasMetaMaskOrWeb3Available =
      MetaMaskOnboarding.isMetaMaskInstalled() ||
      window?.ethereum ||
      window?.web3;

    return (
      <span className="accountInfo">
        {hasMetaMaskOrWeb3Available ? (
          <span className="accountInfo"
            onClick={() => {
              activate(injected, undefined, true).catch((error) => {
                // ignore the error if it's a user rejected request
                if (!(error instanceof UserRejectedRequestError)) {
                  setError(error);
                }
              });
            }}
          >
            {MetaMaskOnboarding.isMetaMaskInstalled()
              ? "Connect to MetaMask"
              : "Connect to Wallet"}
          </span>
        ) : (
            <span className="accountInfo" onClick={() => onboarding.current?.startOnboarding()}>Install Metamask</span>
        )}
      </span>
    );
  }

  return (
    <a className="accountInfo"
      {...{
        href: formatExplorerLink("Account", [chainId, account]),
        target: "_blank",
        rel: "noopener noreferrer",
      }}
    >
      {ENSName || `${shortenHex(account, 4)}`}
    </a>
  );
};

export default Account;
