import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import './assets/css/App.css';
import Footer from './pages/templates/Footer';
import Header from './pages/templates/Header';
import Menu from './pages/templates/Menu';
import { useWeb3React } from '@web3-react/core';
import Home from './pages/Home';
import NotConnected from './pages/NotConnected';

function App() {
  const { account, library } = useWeb3React();

  const isOwner = false;

  const isConnected = typeof account === "string" && !!library;

  return (
    <Router>
      <div className="body-container">
        <Header /><br /><br />
        <Menu isConnected={isConnected} />

        { isConnected ? // is connected to metamask
          <Switch>
            <Route exact path="/">
              <Home />
            </Route>
            { isOwner ? // is owner
              null
            : // is not owner
              null
            }
          </Switch>
        : // is not connected to metamask
          <Switch>
            <Route exact path="/">
              <NotConnected />
            </Route>
          </Switch>
        }

        <Footer />
      </div>
    </Router>
  );
}

export default App;
